// import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
	export default {
		/* components:{
			uniNumberBox
		}, */
		data:function(){
			return {
				background: ['color1', 'color2', 'color3'],
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				duration: 500,
				obje:'',
				title:[
					{name:'商品介绍'},
					{name:'规格参数'},
					{name:'包装清单'},
					{name:'售后服务'},
				],
				ins:0,
				min:0,
				max:0
			}
		},
		mounted(){
			this.homeshopping()
			
		},
		methods:{
			homeshopping:function(){
				var arr = new Array()
				arr = JSON.parse(localStorage.getItem('home'))
				 if(arr.index == this.$store.state.homedata[arr.id].id){
					this.obje = JSON.parse(localStorage.getItem('home'))
					console.log(this.obje)
				  }else{
					  console.log('商品id不一致')
				  }
			},
			active:function(item,index){
				this.ins = index
			},
			join:function(){
				uni.showToast({
				    title: '亲我在购物车等着您哦!',
				    duration: 2000
				});
				localStorage.setItem('shopping',JSON.stringify(this.obje))  //购物车数据备用
			}
		}
	}
